import React from 'react';
import Card from 'react-bootstrap/Card'
import Button from 'react-bootstrap/Button'
import './Projects.css';

const baseurl = `https://api.github.com/users/`
const repos = `${baseurl}${process.env.REACT_APP_USERNAME}/repos`;

class Projects extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      items: []
    };
  }

  componentDidMount() {
    fetch(repos)
      .then(res => res.json())
      .then(
        (result) => {
          this.setState({
            isLoaded: true,
            items: result
          });
        },
        (error) => {
          this.setState({
            isLoaded: true,
            error
          });
        }
      )
  }

  createCard(item) {
    return (
      <Card className="project-card" key={item.id}>
        <Card.Body>
          <Card.Title>{item.name}</Card.Title>
          <Card.Text>
            {item.description}
          </Card.Text>
          <Button variant="primary" href={item.html_url} target="_blank">Go to project</Button>
        </Card.Body>
      </Card>
    )
  }

  render() {
    const { error, isLoaded, items } = this.state;
    if (error) {
      return <div>Error: {error.message}</div>;
    } else if (!isLoaded) {
      return <div>Loading...</div>;
    } else {
      return (
        <div id="project-list" className="d-flex flex-wrap project-box">
          {items.map(item => (
            this.createCard(item)
          ))}
        </div>
      );
    }
  }
}

export default Projects;